/* Swiper global CSS */

.swiper-container {
    width: 100%;
    height: 100%;
}
  
/* Swiper navigation button */
.swiper-button-prev::after,.swiper-button-next::after {
    display: none;
}

/* Custom Swiper CSS */

/* Custom swiper arrow */

.swiper-container__arrow {
    background: $secondary;
    width: 40px;
    height: 40px;
    border-radius: 50%; 

    &-top-choices {
        top: 87.5px;
    }

    &-get-inspiration {
        top: 90.5px; 

        @include media-breakpoint-up(md) {
            top: 79px;
        }

        @include media-breakpoint-up(lg) {
            top: 131.5px;
        }
    }
}


.swiper-container__arrow--sm {
    background: $secondary;
    width: 24px;
    height: 24px;
    border-radius: 50%; 
    
    .material-icons {
        font-size: 14px;
    }

    @include media-breakpoint-up(md) {
        width: 30px;
        height: 30px;
    }
}